import React from "react";

import { Row, Column } from "components/Grid.tsx";
import arrow from "assets/img/link-arrow-gray.svg";
import statement from "assets/img/icons/orange/statement.svg";
import { resourceMessages } from "./messages";
import {
  Section,
  Title,
  DescriptionTitle,
  DescriptionSubtitle,
  ResourcesWrapper,
  Card,
  CardImage,
  CardTitle,
  LinkIcon,
  BlogLinksWrapper,
  BlogLink,
  BlogIcon,
  TitleIcon,
} from "./elements";

const Resources = () => (
  <Section>
    <Row gutterWidth={26} column={30}>
      <Column span={1} spanMedium={0} />
      <Column span={11} spanMedium={12}>
        <Title>Help me with</Title>
      </Column>
    </Row>
    {resourceMessages.map((section) => (
      <Row key={section.title} gutterWidth={26} column={30} withTopMargin>
        <Column span={1} spanMedium={0} />
        <Column span={4} spanMedium={12}>
          <DescriptionTitle>
            <TitleIcon src={section.titleIcon} />
            {section.title}
          </DescriptionTitle>
          <DescriptionSubtitle>{section.subtitle}</DescriptionSubtitle>
        </Column>
        <Column span={7} spanMedium={12}>
          <ResourcesWrapper>
            <Card to={section.cardLinkTo}>
              <CardImage image={section.cardImage} />
              <CardTitle>
                <LinkIcon src={arrow} />
                {section.cardTitle}
              </CardTitle>
            </Card>
            <BlogLinksWrapper>
              {section.blogLinks.map((blog) => (
                <BlogLink key={`${section.title}-${blog.title}`} to={blog.to}>
                  <BlogIcon src={statement} />
                  {blog.title}
                </BlogLink>
              ))}
            </BlogLinksWrapper>
          </ResourcesWrapper>
        </Column>
      </Row>
    ))}
  </Section>
);

export default Resources;
